import React, { Fragment } from 'react';
import { Link, Route } from 'react-router-dom';

import style from './Parish.module.css';
import top_image from '../assets/images/parish_top_image.png';
import SupportUs from './SupportUs/SupportUs';
import ParishHistory from './ParishHistory/ParishHistory';
import OurChurch from './OurChurch/OurChurch';

const navElements = [
    { href: '/parish', text: 'PARISH HISTORY' },
    { href: '/parish/our_church', text: 'OUR CHURCH' },
    { href: '/parish/life_and_work', text: 'LIFE AND WORK' },
    { href: '/parish/troicky_vestnik', text: '"TROICKY VESTNIK"' },
    { href: '/parish/support_us', text: 'SUPPORT US' }
];

const parish = () => (
    <Fragment>
        <div className={style.ImageWrapper}>
            <img className={style.Image} src={top_image} alt='' />
        </div>
        <section className={style.MainWrapper}>
            <div className={style.MainBox}>
                <div className={style.HeaderTopNav__Items}>
                    {navElements.map(element => (
                        <Link className={style.HeaderTopNav__Item} to={element.href}>
                            <div>{element.text}</div>
                        </Link>
                    ))}
                </div>
            </div>
        </section>
        <Route path='/parish' exact>
            <ParishHistory />
        </Route>
        <Route path='/parish/support_us' exact>
            <SupportUs />
        </Route>
        <Route path='/parish/our_church' exact>
            <OurChurch />
        </Route>
    </Fragment>
)

export default parish;